@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard-dynamic-subset.css');

.wrapper {
    position: absolute;
    width: 1920px;
    height: 266px;
    bottom: 0;
    background-color: #FAFAFA;
}

.leftContents {
    margin-left: 268px;
}

.serviceCenter {
    width: 48px;
    height: 17px;
    margin-top: 34px;
    margin-bottom: 12px;
    font-family: "Pretendard";
    font-style: normal;
    font-weight: 700;
    font-size: 13px;
    line-height: 16px;
}

.operatingTimeInfo {
    width: 343px;
    height: 30px;
    margin-bottom: 12px;
    font-family: "Pretendard";
    font-style: normal;
    font-weight: 400;
    font-size: 11px;
    line-height: 15px;
    color: #828282;
}

.oneOnOneBox {
/* box-sizing: border-box; */
display: flex;
flex-direction: row;
justify-content: center;
align-items: center;
padding: 6px 8px;
gap: 10px;

width: 51px;
height: 25px;

background: #FFFFFF;
border: 0.5px solid #E0E0E0;
}

.oneOnOneText {
width: 35px;
height: 13px;
font-family: "Pretendard";
font-style: normal;
font-weight: 600;
font-size: 11px;
color: #4F4F4F;
flex: none;

}

.FAQAndTips {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 151px;
    margin-top: 16px;
}

.FAQ {
font-family: "Pretendard";
font-style: normal;
font-weight: 600;
font-size: 11px;
line-height: 13px;
border-right: 0.5px solid #BDBDBD;
padding-right: 8px;
margin-right: 8px;
color: #4F4F4F;
}


.usefulTips {
font-family: "Pretendard";
font-style: normal;
font-weight: 600;
font-size: 11px;
line-height: 13px;
color: #4F4F4F;
}

.termsOfUse,
.privacyStatement {
font-family: 'Pretendard';
font-style: normal;
font-weight: 600;
font-size: 11px;
line-height: 13px;
color: #4F4F4F;
}

.termsOfUse{
margin-top: 24px;
}

.privacyStatement {
margin-top: 10px;
}